import { hexToRgb, rgba, hexToRgba } from './color';

const getRuleStr = ({ className, primaryColor, secondaryColor }) => (`
  .${className} {
    position: absolute;
    opacity: 0;
    font-family: inherit;
    background-color: ${rgba(primaryColor)};
    border: 1px solid ${rgba(secondaryColor, .5)};
    border-radius: 3px;
    box-shadow: 0 2px 20px rgba(0,0,0, .15);
    transition: opacity .2s ease-in-out;
  }
  .${className}-anchor {
    display: inline-block;
    position: absolute;
    bottom: 100%;
    left: 50%;
    transform: translateX(-50%);
    width: 0;
    height: 0;
    border-color: transparent;
    border-bottom-color: ${rgba(secondaryColor, .5)};
    border-style: solid;
    border-width: 0 6px 6px 6px;
  }

  .${className}-primaryBox {
    padding: 10px;
    background-color: ${rgba(secondaryColor, .1)};
  }
  .${className}-actionBox {
    position: relative;
    width: 240px;
    height: 240px;
    text-align: center;
    cursor: pointer;
    background-color: ${rgba(primaryColor)};
    border: 2px dashed ${rgba(secondaryColor, 1)};
  }
  .${className}-actionBox-content {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    transform: translate(-50%, -50%);
    display: block;
  }
  .${className}-actionBox-content-picWrap {
    display: block;
    margin-bottom: 20px;
  }
  .${className}-actionBox-content-pic {
    display: inline-block;
    color: ${rgba(secondaryColor)};
  }
  .${className}-actionBox-content-pic svg {
    display: block;
    width: 64px;
    height: 64px;
  }
  .${className}-actionBox-content-choose {
    display: inline-block;
    padding-bottom: 4px;
    border-bottom: 2px solid ${rgba(secondaryColor)};
    font-weight: bold;
    color: ${rgba(secondaryColor)};
  }
  .${className}-actionBox-content-drag {
    margin-top: 10px;
    color: ${rgba(secondaryColor, .5)};
  }
  .${className}-actionBox-file-chooser {
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 1px;
    height: 1px;
    opacity: 0;
  }

  .${className}-actions {
    padding: 10px;
    text-align: center;
  }
  .${className}-actionList {
    list-style-type: none;
    font-size: 0;
    margin: 0;
    padding-left: 0;
  }
  .${className}-actionList-item {
    display: inline-block;
  }
  .${className}-actionList-item:not(:last-child) {
    margin-right: 10px;
  }
  .${className}-actionBtn {
    position: relative;
    width: 32px;
    height: 32px;
    border-radius: 3px;
    background-color: ${rgba(secondaryColor, .5)};
    color: ${rgba(primaryColor)};
    cursor: pointer;
  }
  .${className}-actionBtn.is-selected {
    background-color: ${rgba(secondaryColor)};
  }
  .${className}-actionBtn svg {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: block;
    width: 18px;
    height: 18px;
  }

  .${className}-progress {
    padding: 10px;
    text-align: center;
    border-top: 2px solid ${rgba(secondaryColor, .1)};
  }
  .${className}-progressList {
    list-style-type: none;
    margin: 0;
    font-size: 0;
    padding-left: 0;
  }
  .${className}-progressList-item {
    display: inline-block;
    width: 6px;
    height: 6px;
    border-radius: 100%;
    background-color: ${rgba(secondaryColor, .25)};
  }
  .${className}-progressList-item:not(:last-child) {
    margin-right: 4px;
  }
  .${className}-progressList-item.is-selected {
    background-color: ${rgba(secondaryColor)};
  }
`);

export const getRules = ({ theme = 'light', color, className }) => {
  const primaryColor = theme === 'light' ? hexToRgb('#fff') : hexToRgb('#555');
  const secondaryColor = hexToRgb(color);
  return (
    getRuleStr({ className, primaryColor, secondaryColor })
      .split(/\}\n[\s]*\./g)
      .filter((r) => !!r)
      .map((r, i, arr) => {
        if (i === 0) {
          return `${r}}`;
        } else if (i === arr.length - 1) {
          return `.${r}`;
        } else {
          return `.${r}}`;
        }
      })
  );
};
