// import { h, render } from 'preact';
import Events from './events';
import { defer } from './util';
import { getRules } from './style';
import { getHtml } from './html';
import { PhotoBoxTarget, NullPhotoBoxTarget } from './target';

class PhotoBox {
  constructor(options = {}) {
    this.$container = document.querySelector('body');
    this.events = new Events();
    const defaults = {
      theme: 'light',
      color: '#455054',
      className: 'PictureBox',
    };
    this.options = Object.assign({}, defaults, options);
    this.opened = false;
    this.inited = false;
    this.target = new NullPhotoBoxTarget();

    this._handleDocumentClick = this._handleDocumentClick.bind(this);
    this._handleDocumentKeyup = this._handleDocumentKeyup.bind(this);
  }
  _handleDocumentClick(e) {
    this.close();
  }
  _handleDocumentKeyup(e) {
    if (e.keyCode === 27) {
      this.close();
    }
  }
  init() {
    document.addEventListener('click', this._handleDocumentClick);
    document.addEventListener('keyup', this._handleDocumentKeyup);

    this.$el = this.createEl();
    this.$el.addEventListener('click', (e) => { e.stopPropagation(); });
    this.$container.appendChild(this.$el);

    this.sheet = this.createStyle();
    this.inited = true;

    return this;
  }
  destroy() {
    if (!this.inited) {
      throw new Error('Must be initialized before destroying.');
    }

    this.$el.parentNode.removeChild(this.$el);
    this.$style.parentNode.removeChild(this.$style);

    document.removeEventListener('click', this._handleDocumentClick);
    document.removeEventListener('keyup', this._handleDocumentKeyup);

    this.target.destroy();
  }
  attach(el) {
    this.target = new PhotoBoxTarget(this, el);
    this.target.init();
  }
  createEl() {
    const el = document.createElement('div');
    el.classList.add(this.options.className);
    // render(<div className="PictureBox-boom" />, el);
    el.innerHTML = getHtml(this.options);

    defer(() => {
      const $ab = el.querySelector(`.${this.options.className}-actionBox`);
      const $fc = el.querySelector(`.${this.options.className}-actionBox-file-chooser`);
      $ab.addEventListener('click', (e) => {
        var event = new MouseEvent('click', {
          'view': window,
          'bubbles': false,
          'cancelable': true
        });
        $fc.dispatchEvent(event);
      });
    });
    return el;
  }
  createStyle() {
    const $style = document.createElement('style');
    document.head.insertBefore($style, document.head.firstChild);
    getRules(this.options).forEach((rule, i) => {
      $style.sheet.insertRule(rule, i);
    });
    return $style.sheet;
  }
  toggle() {
    this.opened ? this.close() : this.open();
  }
  open() {
    this.opened = true;
    this.$el.style.opacity = 1;
    this.$el.style.pointerEvents = 'auto';
    this.target.position();
  }
  close() {
    this.$el.style.opacity = 0;
    this.$el.style.pointerEvents = 'none';
    this.opened = false;
  }
  setPosition({ top, left }) {
    (window.requestIdleCallback || window.setTimeout)(() => {
      this.$el.style.top = `${top}px`;
      this.$el.style.left = `${left}px`;
      this.events.fire('position', { top, left });
    });
  }
}

export default PhotoBox;
